

html {
    min-width: 960px;
}
body {
    min-width: 960px;
    height: 100%;
    padding: 0;
    margin: 0;
    font-family: "Courier", "TronicaMono-Regular", "Arial", times;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 17px;

    background-color: rgb(0, 0, 0);
}
a {
    color: inherit;
    text-decoration: inherit;
}
