/***** HEXAGONS *****/
.hex-main {
    display: flex;
    --s: 110px; /* size  */
    --m: 3px; /* margin */
    --f: calc(var(--s) * 1.732 + 4 * var(--m) - 1px); 
    min-height: 250px;
}
.hex-container {
    font-size: 0; /* disable white space between inline block element */
}
.hex-container::before {
    content: "";
    width: calc(var(--s) / 2 + var(--m));
    float: left;
    height: 120%; 
    shape-outside: repeating-linear-gradient(rgb(0,0,0,0), rgb(0,0,0,0) 0 calc(var(--f) - 3px), rgb(0,0,0) 0 var(--f));
}
.hex-container img {
    width: var(--s);
    margin: var(--m);
    height: calc(var(--s) * 1.1547);
    display: inline-block;
    font-size: initial;
    margin-bottom: calc(var(--m) - var(--s) * 0.2885);
}

