@media (max-width: 960px) {
  html {
      min-width: 100%;
  }
  body {
      min-width: 100%;
  }
  
  #root {
      min-width: 100%;
  }

  #app {
      min-width: 100%;
  }

  h1 {
      font-size: 35px;
  }
  h2 {
      font-size: 35px;
      text-shadow: 0 4px rgb(108, 21, 15);
      line-height: 150%;
      white-space: nowrap;
      margin: 0;
      padding: 60px 0;
  }
  
  .archetype {
      padding: 50px 15px 50px 15px;
      text-align: left;
  }

  #section-header {
      min-height: none;
  }
  #cubes {
      padding: 0;
      height: 15%;
  }
  #logo {
      padding: 25px;
      height: 12%;
      display: flex;
      justify-content: center;
      flex-direction: column;
  }
  #logo-subtext {
      padding: 5px 25px 50px 25px;
  }

  #section-about {
      background-size: 100% auto;
  }
  #section-about-content {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 0px 25px;
  }
  #about-text {
      max-width: none;
  }
  
  #about-text > p {
      font-size: 0.7em;
  }
  #tesseract {
      display: flex;
      flex-direction: column;
      justify-content: center;
      overflow: visible;
      padding-bottom: 200px;
  }
  #tesseract img {
      height: auto;
      width: 100%;
  }

  #section-roadmap ul {
      padding: 0;
  }
  .bullet-text-single, .bullet-text-double {
      display: none;
  }
  .bullet-text-mobile {
      display: block;
      padding: 12px 0 15px 27px;
  }
  .dotted-border-left > p {
      font-size: 12px;
  }
  .roadmap-item > h3 {
      display: none;
  }
  .dotted-border-left > h3 {
      display: unset;
      padding: 0;
  }

  .hex-main {
      --s: 100px;
  }

  #section-team {
      background-size: unset;
      background-attachment: unset;
      background-repeat: repeat;
  }

  .teammember {
      width: auto;
  }
  
  #footer-logo {
      padding-bottom: 25px;
  }
  #footer-logo img {
      width: 100%;
  }



  #icons {
      display: none;
  }
  #top-bar {
      padding: 0 0 25px 0;
      height: unset;
  }
  #links {
      display: unset;
      padding: 0;
  }
  #links > ul {
      display: none;
      flex-direction: column;
      justify-content: flex-start;
      width: 100%;
  }
  #links > ul > li {
      height: 60px;
      line-height: 60px;
      background-color: rgb(19, 19, 19);
      text-align: left;
      padding-left: 25px;
  }
  #links > ul > li:first-child > img {
      width: 25px;
      display: inline-block;
      vertical-align: middle;
      padding-left: 25px;
  }
  #menu-container {
      display: none;
  }
  #menu-toggler {
      height: 60px;
      line-height: 60px;
      background-color: rgb(19, 19, 19);
      text-align: left;
      padding-left: 25px;
      display: blocK;
  }
  #menu-toggler > img {
      vertical-align: middle;
  }
  #wallet {
      display: none;
  }
  #wallet-phone {
      display: block;
      width: 300px;
      margin: 15px auto;
  }
  #wallet-phone button {
      background: white;
      border: none;
      font: inherit;
      font-size: 21px;
      cursor: pointer;
      outline: inherit;
      white-space: nowrap;
      padding: 7px 23px;
  }

  #section-mint {
      background-position: bottom left;
  }
  #mint-square-inner {
      padding: 30px;
      flex-direction: column;
  }
  #mint-square-content > h2 {
      font-size: 25px;
      padding: 25px 5px;
  }
  #mint-square-content {
      padding: 0;
  }
  #mint-amount {
      font-size: 17px;
      padding-bottom: 30px;
  }
  #mint-price {
      font-size: 14px;
      padding-bottom: 30px;
  }

  
  #section-questions {
      flex-direction: column;
  }
  #faq-header {
      margin: auto;
  }
  .question-header {
      font-size: 15px;
  }
  .question-text {
      font-size: 12px;
  }

  
  #section-roadmap {
      background-size: 350px;
  }
  .partner-container > div {
      max-width: 45%;
  }

}
@media (max-width: 730px) {
  .hex-main {
      width: 220px;
      --s: 65px;
  }
  .roadmap-phase03-stonez img {
      width: 65px;
  }
  .roadmap-phase02-stonez img {
      width: 65px;
  }
  #roadmap-phase02-last-stonez {
      font-size: 20px;
  }
}

@media (max-width: 1462px) {
  #mint-square-img-container {
      max-height: unset;
  }
  #mint-square-img-container > img {
      max-width: 100%;
      
      position: unset;
      top: unset;
      transform: unset;
    
  }
}

@media (max-width: 515px) {
  #roadmap-future {
      display: none;
  }
  #roadmap-container li.roadmap-future-phone {
      display: unset;
  }
  p.big-questionmark {
      text-align: left;
  }
  .no-border-questionmark {
      padding-left: 27px;
  }
}

@media (min-width: 1920px) {
  #section-mint {
      background-size: 100% auto;
  }
}